
.sim-bulkcheck {
  padding: 0;
  background: transparent;
  color: var(--bulkcheck-color, var(--action));
  -webkit-box-shadow: none;
          box-shadow: none;
  white-space: nowrap;
}
.sim-bulkcheck[tooltip] {
    position: relative;
}
.sim-bulkcheck:active, .sim-bulkcheck:focus {
    -webkit-box-shadow: none;
            box-shadow: none;
}
.sim-bulkcheck.active {
    color: inherit;
}
.sim-bulkcheck .sim-icontext {
    opacity: .4;
    text-shadow: -1px 0 1px var(--shadow);
    position: relative;
}
.sim-bulkcheck .sim-icontext ~ * {
      opacity: .7;
      margin-left: -1.25em;
}
.sim-bulkcheck .sim-icontext ~ * + * {
        opacity: 1;
}



