
.sim-progress {
  position: relative;
  margin-right: 1em;
  height: .6em;
  background: var(--progress-bg, var(--edge));
  border-radius: .3em;
}
.sim-progress__completed {
    width: var(--percent);
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    border-radius: .3em;
    background: var(--progress-fg, var(--purple));
}



