
.sim-modal {
  position: fixed;
  z-index: 5;
  will-change: transform;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-transform: scale(0);
          transform: scale(0);
  opacity: 0;
  -webkit-transition: opacity var(--ms, var(--default-ms)) linear;
  transition: opacity var(--ms, var(--default-ms)) linear;
}
.sim-modal.active {
    -webkit-transform: scale(1);
            transform: scale(1);
    opacity: 1;
}
.sim-modal.active .sim-modal__wrapper {
      -webkit-transform: translateY(0%) scale(1);
              transform: translateY(0%) scale(1);
}
.sim-modal.active .sim-modal__dismiss {
      -webkit-transform: translate(-50%, -50%) scale(1);
              transform: translate(-50%, -50%) scale(1);
}
.sim-modal__mask {
    background: rgba(0, 0, 0, 0.65);
    position: absolute;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
}
.sim-modal__wrapper {
    position: relative;
    width: 65vw;
    max-width: 60em;
    min-width: 30em;
    max-height: 65vh;
    height: 30em;
    background: var(--modal-bg, var(--lightest));
    color: var(--modal-fg, var(--darker));
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    border-radius: .2em;
    -webkit-box-shadow: 0 0 0 1px rgba(0, 0, 0, 0.2), 0 1em 2em -1em;
            box-shadow: 0 0 0 1px rgba(0, 0, 0, 0.2), 0 1em 2em -1em;
    -webkit-transform: translateY(-50%) scale(0.8);
            transform: translateY(-50%) scale(0.8);
    -webkit-transition: -webkit-transform var(--ms, var(--default-ms)) linear;
    transition: -webkit-transform var(--ms, var(--default-ms)) linear;
    transition: transform var(--ms, var(--default-ms)) linear;
    transition: transform var(--ms, var(--default-ms)) linear, -webkit-transform var(--ms, var(--default-ms)) linear;
}
.sim-modal__dismiss {
    line-height: 0;
    white-space: nowrap;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -webkit-transition: all var(--ms, var(--default-ms)) ease-out;
    transition: all var(--ms, var(--default-ms)) ease-out;
    -webkit-transition-delay: calc(var(--ms, var(--default-ms)) / 2);
            transition-delay: calc(var(--ms, var(--default-ms)) / 2);
    -webkit-transform: translate(-50%, 50%) scale(0);
            transform: translate(-50%, 50%) scale(0);
}



