
.sim-tag {
  color: var(--medium-grey);
  background: var(--light);
  display: -webkit-inline-box;
  display: -ms-inline-flexbox;
  display: inline-flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  border: 1px solid;
  border-radius: .2em;
  font-size: .7em;
  text-transform: uppercase;
  padding: 0 .3em;
  opacity: .7;
  cursor: default;
}
.sim-tag * + span {
    margin-left: .5em;
}
.sim-tag .fa {
    -webkit-box-flex: 0;
        -ms-flex: 0 0 auto;
            flex: 0 0 auto;
}



