
.sim-panel {
  z-index: 3;
  position: fixed;
  will-change: transform;
  width: var(--panel-width, var(--element-width, 30em));
  max-width: 100vw;
  height: 100vh;
  top: 0;
  left: 0;
  -webkit-transform: translateX(var(--panel-parallax-start, 100vw));
          transform: translateX(var(--panel-parallax-start, 100vw));
}
.sim-panel.active {
    -webkit-transform: translateX(var(--panel-parallax-end, calc(100vw - var(--panel-width, var(--element-width, 30em)))));
            transform: translateX(var(--panel-parallax-end, calc(100vw - var(--panel-width, var(--element-width, 30em)))));
}
.sim-panel.active .sim-panel__dismiss {
      -webkit-transform: translate(-50%, 50%) scale(1);
              transform: translate(-50%, 50%) scale(1);
}
.sim-panel__dismiss {
    line-height: 0;
    white-space: nowrap;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -webkit-transition: all var(--ms, var(--default-ms)) ease-out;
    transition: all var(--ms, var(--default-ms)) ease-out;
    -webkit-transition-delay: calc(var(--ms, var(--default-ms)) / 2);
            transition-delay: calc(var(--ms, var(--default-ms)) / 2);
    -webkit-transform: translate(-50%, 50%) scale(0);
            transform: translate(-50%, 50%) scale(0);
}
.sim-panel {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  background: var(--panel-bg, var(--darker));
  color: var(--panel-fg, var(--lightest));
  -webkit-box-shadow: 0.2em 0 0 0 var(--panel-accent, var(--accent)) inset;
          box-shadow: 0.2em 0 0 0 var(--panel-accent, var(--accent)) inset;
  border-left: 0.2em solid var(--panel-accent, var(--accent));
  padding: 1em;
}
.sim-panel__dismiss {
    position: absolute;
    z-index: 1;
    top: 0;
    left: 0;
    padding: 0;
    font-family: monospace;
    font-size: 3em;
    font-weight: 100;
    cursor: pointer;
    width: 1em;
    height: 1em;
    color: var(--panel-x-fg, var(--lightest));
    background: var(--panel-x-bg, var(--accent));
    border-radius: 50%;
    text-decoration: none;
}
.sim-panel__dismiss:hover {
      text-decoration: none;
      color: var(--panel-x-fg-hover, var(--lightest));
      background: var(--panel-x-bg-hover, var(--action));
}
.sim-panel.active .sim-panel__dismiss {
    -webkit-transition-delay: 0ms;
            transition-delay: 0ms;
    -webkit-transition-duration: calc(var(--ms, var(--default-ms)) / 2);
            transition-duration: calc(var(--ms, var(--default-ms)) / 2);
}
.sim-panel.active .sim-panel__dismiss:hover {
      -webkit-transform: translate(-50%, 50%) scale(1);
              transform: translate(-50%, 50%) scale(1);
}
.sim-panel.active .sim-panel__dismiss:active {
      -webkit-transform: translate(-50%, 50%) scale(0.9);
              transform: translate(-50%, 50%) scale(0.9);
}
.sim-panel header {
    -webkit-box-flex: 0;
        -ms-flex: none;
            flex: none;
    padding: 1em;
}
.sim-panel__content {
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
    overflow: auto;
    position: relative;
    z-index: 1;
    padding: 1em;
}
.sim-panel footer {
    -webkit-box-flex: 0;
        -ms-flex: none;
            flex: none;
    padding: 1em;
}



