
.sim-bubble {
  position: absolute;
  top: calc(var(--y) * 1px);
  z-index: 2;
  -webkit-transition: 600ms ease-out;
  transition: 600ms ease-out;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
}
.sim-bubble--content {
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    color: var(--bubble-fg, var(--lightest));
    background: var(--bubble-bg, var(--dark));
    border-radius: .3em;
    -webkit-box-shadow: 0 0.5em 2em -0.3em rgba(0, 0, 0, 0.5);
            box-shadow: 0 0.5em 2em -0.3em rgba(0, 0, 0, 0.5);
    overflow: hidden;
}
.sim-bubble::after {
    content: '';
    position: absolute;
    top: 50%;
    pointer-events: none;
    -webkit-transition: top 80ms ease-out;
    transition: top 80ms ease-out;
    border: 1em solid transparent;
    border-width: 1.2em .8em;
}
.sim-bubble--left {
    left: calc(var(--x) * 1px);
}
.sim-bubble--left::after {
      left: 1px;
      border-right-color: var(--dark);
      -webkit-transform: translate(-100%, -50%);
              transform: translate(-100%, -50%);
}
.sim-bubble--right {
    right: calc(var(--x) * 1px);
}
.sim-bubble--right::after {
      left: 100%;
      border-left-color: var(--dark);
      -webkit-transform: translate(-1px, -50%);
              transform: translate(-1px, -50%);
}
.sim-bubble--dismiss {
    line-height: 0;
    white-space: nowrap;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -webkit-transition: all var(--ms, var(--default-ms)) ease-out;
    transition: all var(--ms, var(--default-ms)) ease-out;
    -webkit-transition-delay: calc(var(--ms, var(--default-ms)) / 2);
            transition-delay: calc(var(--ms, var(--default-ms)) / 2);
    -webkit-transform: translate(50%, -50%);
            transform: translate(50%, -50%);
    position: absolute;
    z-index: 1;
    top: 0;
    right: 0;
    padding: 0;
    font-family: monospace;
    font-weight: 100;
    cursor: pointer;
    width: 2em;
    height: 2em;
    color: var(--bubble-x-fg, var(--lightest));
    background: var(--bubble-x-bg, var(--accent));
    border-radius: 50%;
    text-decoration: none;
}
.sim-bubble--dismiss:hover {
      text-decoration: none;
      color: var(--bubble-x-fg-hover, var(--lightest));
      background: var(--bubble-x-bg-hover, var(--action));
}
.sim-bubble-enter, .sim-bubble-leave-to {
    opacity: 0;
}
.sim-bubble-enter-active, .sim-bubble-leave-active {
    -webkit-transition: opacity 160ms linear;
    transition: opacity 160ms linear;
}



