
.sim-overlay {
  z-index: 2;
  position: fixed;
  will-change: transform;
  width: 100vw;
  height: 100vh;
  top: 0;
  left: 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-transform: translateY(var(--overlay-parallax-start, -100%));
          transform: translateY(var(--overlay-parallax-start, -100%));
}
.sim-overlay.active {
    -webkit-transform: translateY(var(--overlay-parallax-end, 0%));
            transform: translateY(var(--overlay-parallax-end, 0%));
}
.sim-overlay.active .sim-overlay__dismiss {
      -webkit-transform: translate(-50%, 50%) scale(1);
              transform: translate(-50%, 50%) scale(1);
}
.sim-overlay--content {
    overflow: auto;
}
.sim-overlay__dismiss {
    line-height: 0;
    white-space: nowrap;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -webkit-transition: all var(--ms, var(--default-ms)) ease-out;
    transition: all var(--ms, var(--default-ms)) ease-out;
    -webkit-transition-delay: calc(var(--ms, var(--default-ms)) / 2);
            transition-delay: calc(var(--ms, var(--default-ms)) / 2);
    -webkit-transform: translate(-50%, 50%) scale(0);
            transform: translate(-50%, 50%) scale(0);
}
.sim-overlay {
  color: var(--overlay-fg, var(--dark));
  background: var(--overlay-bg, var(--lightest));
  max-width: var(--overlay-width, 100vw);
  margin: var(--overlay-margin, 0 auto);
}
.sim-overlay__dismiss {
    position: absolute;
    top: 0;
    right: 0;
    padding: 0;
    font-family: monospace;
    font-size: 3em;
    font-weight: 100;
    cursor: pointer;
    width: 1em;
    height: 1em;
    color: var(--overlay-x-fg, var(--lightest));
    background: var(--overlay-x-bg, var(--accent));
    border-radius: .5em;
    text-decoration: none;
}
.sim-overlay__dismiss:hover {
      text-decoration: none;
      color: var(--overlay-x-fg-hover, var(--lightest));
      background: var(--overlay-x-bg-hover, var(--action));
}
.sim-overlay.active .sim-overlay__dismiss {
    -webkit-transition-delay: 0ms;
            transition-delay: 0ms;
    -webkit-transition-duration: calc(var(--ms, var(--default-ms)) / 2);
            transition-duration: calc(var(--ms, var(--default-ms)) / 2);
}
.sim-overlay.active .sim-overlay__dismiss:hover {
      -webkit-transform: translate(-50%, 50%) scale(1);
              transform: translate(-50%, 50%) scale(1);
}
.sim-overlay.active .sim-overlay__dismiss:active {
      -webkit-transform: translate(-50%, 50%) scale(0.9);
              transform: translate(-50%, 50%) scale(0.9);
}



