
.sim-filter {
  --active-color: var(--filter-active-color, var(--green, #9ccc66));
}
.sim-filter + .sim-filter {
    margin-top: 1em;
}
.sim-filter--header {
    padding: 0;
    display: block;
    border: 0;
    white-space: nowrap;
}
.sim-filter .sim-selection {
    font-size: .8em;
}
.sim-filter .sim-selection input:checked {
      --selection-color: var(--active-color);
}
.sim-filter.active .sim-filter--header {
    color: var(--active-color);
}
.sim-accordion--label {
  cursor: pointer;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: baseline;
      -ms-flex-align: baseline;
          align-items: baseline;
}
.sim-accordion--label::before {
    content: '\f0da';
    display: inline-block;
    position: relative;
    margin: 0 .5em;
    font-family: FontAwesome;
    font-size: 1.3em;
    -webkit-transform: rotate(0);
            transform: rotate(0);
    -webkit-transition: -webkit-transform calc(var(--ms, 300ms) / 2) ease-out;
    transition: -webkit-transform calc(var(--ms, 300ms) / 2) ease-out;
    transition: transform calc(var(--ms, 300ms) / 2) ease-out;
    transition: transform calc(var(--ms, 300ms) / 2) ease-out, -webkit-transform calc(var(--ms, 300ms) / 2) ease-out;
    opacity: .35;
}
.sim-accordion--label + .sim-accordion--items {
    max-height: 0;
    overflow: hidden;
    -webkit-transition: var(--ms, 300ms) linear;
    transition: var(--ms, 300ms) linear;
    opacity: 0;
}
.sim-accordion--items {
  margin-left: 1.75em;
}
.sim-accordion.open .sim-accordion--label::before {
  -webkit-transform: rotate(90deg);
          transform: rotate(90deg);
}
.sim-accordion.open .sim-accordion--label + .sim-accordion--items {
  opacity: 1;
  max-height: 90vh;
  overflow: auto;
}
.sim-accordion.open .sim-accordion--items {
  padding: .5em 0;
}



