
.sim-datalist ul {
  margin: 0;
  padding: 0;
  list-style: none;
}
.sim-datalist li {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: baseline;
      -ms-flex-align: baseline;
          align-items: baseline;
  -webkit-transition: all var(--ms, var(--default-ms)) cubic-bezier(1, 0.02, 0, 1.05), background 0ms;
  transition: all var(--ms, var(--default-ms)) cubic-bezier(1, 0.02, 0, 1.05), background 0ms;
  -webkit-transition-delay: calc(var(--delay, 0) * 5ms);
          transition-delay: calc(var(--delay, 0) * 5ms);
}
.sim-datalist header:empty,
.sim-datalist footer:empty {
