
.sim-loader {
  font-size: 2em;
  position: absolute;
  z-index: 10;
  top: 1em;
  left: 1em;
  width: 1em;
  height: 1em;
  opacity: 1;
  border-radius: 50%;
  pointer-events: none;
  -webkit-box-shadow: 0 0 0 0.1em var(--loader-ring-color, var(--shade)) inset;
          box-shadow: 0 0 0 0.1em var(--loader-ring-color, var(--shade)) inset;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  will-change: opacity;
}
.sim-loader circle {
    fill: none;
    stroke: var(--loader-color, var(--action));
    stroke-width: 10%;
    stroke-linecap: round;
    -webkit-transform-origin: 50% 50%;
            transform-origin: 50% 50%;
    -webkit-transform: rotate3d(0, 0, 1, 0deg);
            transform: rotate3d(0, 0, 1, 0deg);
    will-change: transform;
    animation: var(--ms-loader-arc, 1656ms) loader-arc ease-in-out infinite, var(--ms-loader-rotate, 1329ms) loader-rotate ease-in-out infinite;
}
.sim-loader--shield {
  position: absolute;
  background: linear-gradient(135deg, var(--fog), var(--mist));
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: 5;
}
@-webkit-keyframes loader-rotate {
to {
    -webkit-transform: rotate3d(0, 0, 1, 360deg);
            transform: rotate3d(0, 0, 1, 360deg);
}
}
@keyframes loader-rotate {
to {
    -webkit-transform: rotate3d(0, 0, 1, 360deg);
            transform: rotate3d(0, 0, 1, 360deg);
}
}
@-webkit-keyframes loader-arc {
from {
    stroke-dasharray: 0 150;
    stroke-dashoffset: 0;
}
to {
    stroke-dasharray: 100 150;
    stroke-dashoffset: -140;
}
}
@keyframes loader-arc {
from {
    stroke-dasharray: 0 150;
    stroke-dashoffset: 0;
}
to {
    stroke-dasharray: 100 150;
    stroke-dashoffset: -140;
}
}
.loader-enter,
.loader-leave-to {
  opacity: 0;
}



