
.sim-tooltip {
  display: inline-block;
  position: relative;
}
.sim-tooltip__cue {
    cursor: default;
}
.sim-tooltip__bubble {
    display: none;
    pointer-events: none;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    opacity: 0;
    position: absolute;
    z-index: 10;
    left: 50%;
    -webkit-transition: all var(--ms, var(--default-ms)) ease-out;
    transition: all var(--ms, var(--default-ms)) ease-out;
}
.sim-tooltip__bubble::after {
      content: '';
      position: absolute;
      left: 50%;
      border: .5em solid transparent;
      -webkit-transform: translate(-50%, -100%);
              transform: translate(-50%, -100%);
}
.sim-tooltip__bubble--up {
      -webkit-transform: translate(-50%, 50%) scale(0.5);
              transform: translate(-50%, 50%) scale(0.5);
      bottom: calc(100% + .5em);
}
.sim-tooltip__bubble--up::after {
        top: calc(100% + 1em - 1px);
}
.sim-tooltip__bubble--up--left {
        -webkit-transform: translate(0%, 50%) scale(0.5);
                transform: translate(0%, 50%) scale(0.5);
        bottom: calc(100% + .5em);
        left: 5%;
}
.sim-tooltip__bubble--up--left::after {
          top: calc(100% + 1em - 1px);
          left: 2em;
}
.sim-tooltip__bubble--down {
      -webkit-transform: translate(-50%, -50%) scale(0.65);
              transform: translate(-50%, -50%) scale(0.65);
      top: calc(100% + .5em);
}
.sim-tooltip__bubble--down::after {
        bottom: calc(100% - 1em - 1px);
}
.sim-tooltip__bubble--down--left {
        -webkit-transform: translate(0%, -50%) scale(0.65);
                transform: translate(0%, -50%) scale(0.65);
        top: calc(100% + .5em);
        left: 5%;
}
.sim-tooltip__bubble--down--left::after {
          bottom: calc(100% - 1em - 1px);
          left: 2em;
}
.sim-tooltip:hover .sim-tooltip__bubble {
    display: block;
}
.sim-tooltip:hover .sim-tooltip__bubble--up, .sim-tooltip:hover .sim-tooltip__bubble--down {
      -webkit-animation: tooltipsVert calc(var(--ms, var(--default-ms)) / 2) ease-out forwards;
              animation: tooltipsVert calc(var(--ms, var(--default-ms)) / 2) ease-out forwards;
}
.sim-tooltip:hover .sim-tooltip__bubble--up--left, .sim-tooltip:hover .sim-tooltip__bubble--down--left {
      -webkit-transform-origin: 0 0;
              transform-origin: 0 0;
      -webkit-animation: tooltipsVertLeft calc(var(--ms, var(--default-ms)) / 2) ease-out forwards;
              animation: tooltipsVertLeft calc(var(--ms, var(--default-ms)) / 2) ease-out forwards;
}
@-webkit-keyframes tooltipsVert {
to {
    opacity: 1;
    -webkit-transform: translate(-50%, 0%) scale(1);
            transform: translate(-50%, 0%) scale(1);
}
}
@keyframes tooltipsVert {
to {
    opacity: 1;
    -webkit-transform: translate(-50%, 0%) scale(1);
            transform: translate(-50%, 0%) scale(1);
}
}
@-webkit-keyframes tooltipsVertLeft {
to {
    opacity: 1;
    -webkit-transform: translate(0%, 0%) scale(1);
            transform: translate(0%, 0%) scale(1);
}
}
@keyframes tooltipsVertLeft {
to {
    opacity: 1;
    -webkit-transform: translate(0%, 0%) scale(1);
            transform: translate(0%, 0%) scale(1);
}
}
@-webkit-keyframes tooltipsHorz {
to {
    opacity: 1;
    -webkit-transform: translate(0%, -50%) scale(1);
            transform: translate(0%, -50%) scale(1);
}
}
@keyframes tooltipsHorz {
to {
    opacity: 1;
    -webkit-transform: translate(0%, -50%) scale(1);
            transform: translate(0%, -50%) scale(1);
}
}
.sim-tooltip__bubble {
  width: var(--tooltip-width, 12em);
  background: var(--tooltip-bg, var(--darker));
  color: var(--tooltip-fg, var(--lightest));
  font-size: .8em;
  padding: .7em 1em;
  border-radius: .3em;
  -webkit-box-shadow: 0 0.5em 2em -0.5em var(--tooltip-shadow, var(--shadow));
          box-shadow: 0 0.5em 2em -0.5em var(--tooltip-shadow, var(--shadow));
}
.sim-tooltip__bubble--up::after, .sim-tooltip__bubble--up--left::after {
    border-top-color: var(--tooltip-bg, var(--darker));
}
.sim-tooltip__bubble--down::after, .sim-tooltip__bubble--down--left::after {
    border-bottom-color: var(--tooltip-bg, var(--darker));
}



