
.sim-autocomplete {
  position: relative;
}
.sim-autocomplete .sim-autocomplete--search {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-shadow: 0 0 0 1px var(--dusk) inset;
            box-shadow: 0 0 0 1px var(--dusk) inset;
    color: var(--autocomplete-search-fg, var(--dark));
    background: var(--autocomplete-search-bg, var(--lighter));
    border-radius: .2em;
}
.sim-autocomplete .sim-autocomplete--search .sim-autocomplete--search--input {
      position: relative;
      font: inherit;
      color: var(--autocomplete-input-fg, var(--autocomplete-search-fg, var(--dark)));
      background: var(--autocomplete-input-bg, transparent);
      border: 0;
      -webkit-box-shadow: none;
              box-shadow: none;
      z-index: 1;
}
.sim-autocomplete .sim-autocomplete--search--icon {
      color: var(--autocomplete-count-fg, var(--autocomplete-search-fg, var(--dark)));
      background: var(--autocomplete-count-bg, transparent);
}
.sim-autocomplete .sim-autocomplete--search--item-count {
      color: var(--autocomplete-count-fg, var(--dusk));
      background: var(--autocomplete-count-bg, transparent);
}
.sim-autocomplete .sim-autocomplete--search--icon, .sim-autocomplete .sim-autocomplete--search--item-count {
      width: 2em;
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-align: center;
          -ms-flex-align: center;
              align-items: center;
      -webkit-box-pack: center;
          -ms-flex-pack: center;
              justify-content: center;
      pointer-events: none;
      z-index: 1;
}
.sim-autocomplete--options-visible input {
    border-bottom-left-radius: 0;
    border-bottom-right-radius: 0;
}
.sim-autocomplete--items {
    position: absolute;
    top: calc(100% + 1px);
    left: 1px;
    right: 1px;
    max-height: 15em;
    overflow: auto;
    color: var(--autocomplete-items-fg, var(--dark));
    background: var(--autocomplete-items-bg, var(--lighter));
    border-radius: 0 0 .2rem .2rem;
    -webkit-box-shadow: 0 0 0 1px var(--fog), 0 0.3em 1em -0.3em var(--shadow);
            box-shadow: 0 0 0 1px var(--fog), 0 0.3em 1em -0.3em var(--shadow);
}
.sim-autocomplete li {
    padding: .5em;
}
.sim-autocomplete li.highlighted {
      color: var(--autocomplete-highlight-fg, var(--blue));
      background: var(--autocomplete-highlight-bg, var(--shade));
}



